import React, {Component} from 'react';
import './styles.scss';
// import searchlogo from '../images/search.svg';
import Dropdown from './Dropdown';
import locations from '../Data/Locations.json'


class Header extends Component{

    render(){
        return(
            <React.Fragment>
                <nav>
                    <img src='data:image/png;base64,'/>
                        <div className="col-1">
                            <button className="btn btn-rect">Order</button>
                        </div>
                        <div className="col-2"><i className="fa fa-search"></i>
                            <input placeholder="Search Location" className="search"/>
                        </div>
                        <div className="col-1"><i className="fa fa-gift"></i>
                            <label>Gifts</label>
                        </div>
                        <div className="col-2">
                            <Dropdown user={locations.user}></Dropdown>
                        </div>
                        <div className="col-1 bor"><span>Cart Balance</span><br></br>

                            <label> ₹ 65</label>
                        </div>
                        <div className="col-1 total"><span className="outer"><i className="fa fa-shopping-bag  icone"></i></span>
                            <div><span>&nbsp; Total Amount</span><br></br>
                            <span id="rate"> ₹ 40</span></div>
                        </div>
                </nav>  
            </React.Fragment>
        )
    }
}

export default Header